<template>
  <div id="chat">
    <div>
      <div id="messageTo" style="position: fixed;left: 0;right: 0;top: 0;">
        <div>{{messageTo}}</div>
      </div>
    </div>
    <div>
      <chatroom :meses="messageGroup"></chatroom>
    </div>
<!--    <div id="uploadFile" style="position: absolute;left: 0;right: 0;bottom: 200px;">-->
<!--      <label for="msgType">messageType</label>-->
<!--      <input id="msgType" type="number" v-model="messageType">-->

<!--    </div>-->
    <div style="position: absolute;left: 0;right: 0;bottom: 0;height: 105px;background-color: white"></div>
    <div id="sendBottom" style="position: fixed;left: 0;right: 0;bottom: 45px;">
      <div style="position: absolute;left: 0;bottom: 0">
        <label>
          <input type="file" id="upload_file" ref="up_file"
                 @click="chooseFile"
                 style="position:absolute;opacity:0;"
                 accept="image/gif,image/jpeg,image/x-png"
                 @change="fillArea">
          <img style="width: 30px;height: 30px;" src="~assets/file.svg">
        </label>
      </div>
      <div style="position: absolute;left: 30px;bottom: 0" @click="changeToText">
      <textarea id="textarea" v-model="messageContent" autofocus
                required @keydown="send" style="width: 300px;" ref="input_text" ></textarea>
      </div>
      <div id="pressButton" style="position: absolute;right: 0;bottom: 0">
        <button id="sendMsgBtn" style="position:absolute;opacity:0;" @click="sendMessage">-></button>
        <img style="width: 40px;height: 30px;" src="~assets/send.svg">
      </div>
    </div>
  </div>
</template>


<script>
  import {request2} from "../../network/request";
  import storage from "../../utils/storage";
  import Chatroom from "../chat/Chatroom";
  import {makeExpandingArea} from "../../utils/textareaFunc";


  export default {
    name: "UserChat",
    components: {
      Chatroom
    },
    data() {
      return {
        messageTo: '',
        messageContent: '',
        messageType: '1',
        path: '/user/chat',
        messageGroup: []
      }
    },
    methods: {
      changeToText() {
        this.messageType = '1'
      },
      chooseFile() {
        this.messageType='2';
      },
      fillArea() {
        this.messageContent = this.$refs.up_file.files[0].name
      },
      getUserChat(obj, dataset) {
        if(!storage.fetch(obj)){
          storage.save(obj, []);
          let msgObj = {
            userName: this.$store.state.globalUserName,
            msgType: this.messageType,
            content: dataset
          }
          // let mes = this.$store.state.globalUserName+','+ dataset
          let messages = storage.fetch(obj)
          messages.push(msgObj)
          storage.save(obj, messages)
        }else {
          let msgObj = {
            userName: this.$store.state.globalUserName,
            msgType: this.messageType,
            content: dataset
          }
          // let mes = this.$store.state.globalUserName+','+ dataset
          let messages = storage.fetch(obj)
          messages.push(msgObj)
          storage.save(obj, messages)
        }
        this.messageGroup = storage.fetch(obj)
        console.log(this.messageGroup)
      },
      sendMessage() {
        if(this.messageTo === 'GROUP'){
          if(this.messageType === '1'){
            //将发送信息记录到双方聊天框内
            this.$store.state.stompClient.send("/app/chat/" + this.messageTo, {}, JSON.stringify(
              {
                userName: this.$store.state.globalUserName,
                msgType: this.messageType,
                msgContent: this.messageContent
              }
            ));
            this.messageContent = ''
            // console.log(this.messageGroup)
          }
          else if(this.messageType === '2'){
            this.messageContent = ''
            let formFile = new FormData();
            let fileObj = this.$refs.up_file.files[0]; // js 获取文件对象
            formFile.append("file", fileObj); //加入文件对象
            request2({
              url: this.$store.state.globalUrl + '/uploadfile',
              method: 'post',
              headers: {'Content-Type': 'multipart/form-data'},
              data: formFile
            }).then(res => {
              console.log(res.data.responseContent)
              // console.log(typeof res.data.responseContent)
              let dataset = JSON.stringify(res.data.responseContent.fileDownloadUri)
              console.log(dataset)
              let obj = this.messageTo
              this.getUserChat(obj, dataset)
              console.log(this.messageGroup)
              this.$store.state.stompClient.send("/app/chat/" + this.messageTo, {}, JSON.stringify(
                {
                  userName: this.$store.state.globalUserName,
                  msgType: this.messageType,
                  msgContent: dataset
                }
              ));
            }).catch(err => {
              console.log('failing', err)
            });

          }
        }
        else {
          if(this.messageType === '1'){
            let obj = this.messageTo
            console.log(obj)
            this.getUserChat(obj, this.messageContent)
            //将发送信息记录到双方聊天框内
            this.$store.state.stompClient.send("/app/chat/" + this.messageTo, {}, JSON.stringify(
              {
                userName: this.$store.state.globalUserName,
                msgType: this.messageType,
                msgContent: this.messageContent
              }
            ));
            this.messageContent = ''
            // console.log(this.messageGroup)
          }
          else if(this.messageType === '2'){
            this.messageContent = ''
            let formFile = new FormData();
            let fileObj = this.$refs.up_file.files[0]; // js 获取文件对象
            formFile.append("file", fileObj); //加入文件对象
            request2({
              url: this.$store.state.globalUrl + '/uploadfile',
              method: 'post',
              headers: {'Content-Type': 'multipart/form-data'},
              data: formFile
            }).then(res => {
              console.log(res.data.responseContent)
              // console.log(typeof res.data.responseContent)
              let dataset = JSON.stringify(res.data.responseContent.fileDownloadUri)
              console.log(dataset)
              let obj = this.messageTo
              this.getUserChat(obj, dataset)
              console.log(this.messageGroup)
              this.$store.state.stompClient.send("/app/chat/" + this.messageTo, {}, JSON.stringify(
                {
                  userName: this.$store.state.globalUserName,
                  msgType: this.messageType,
                  msgContent: dataset
                }
              ));
            }).catch(err => {
              console.log('failing', err)
            });

          }
        }
      },
      send(event) {
        if (event.keyCode === 13 && event.ctrlKey) {
          this.messageContent += "\n"; //换行
        } else if (event.keyCode === 13) {
          this.sendMessage()

          event.preventDefault(); // 阻止浏览器默认换行操作
        }
      }
    },
    created() {

    },
    destroyed() {

    },
    activated() {
      this.messageTo = this.$route.query.id
      // console.log(this.messageTo)
      let obj = this.$route.query.id
      if(!storage.fetch(obj)){
        storage.save(obj, []);
      }else {
        this.messageGroup = storage.fetch(this.$route.query.id)
      }

      // set a timber to refresh msg content in real time
      let _this = this; //声明一个变量指向Vue实例this，保证作用域一致
      this.timer = setInterval(() => {
        if(!storage.fetch(obj)){
          storage.save(obj, []);
        }else {
          _this.messageGroup = storage.fetch(this.$route.query.id)
        }
      }, 1000);

      // console.log(this.messageGroup)
      // this.$router.push(this.path);
    },
    deactivated() {
      if (this.timer) {
        clearInterval(this.timer); // 在Vue实例销毁前，清除我们的定时器
      }
      this.messageContent = ''
    },
    beforeMount() {

    },
    mounted() {
      let textarea = document.getElementById('textarea');
      makeExpandingArea(textarea);
    },

  }
</script>

<style scoped>
  #messageTo {
    width: 100%;
    height: 50px;
    background-color: black;
    color: white;
    text-align: center;
    font-size: 30px;
    line-height: 50px;
    }
  #chatroom{
    white-space: pre-line;

  }
  #textarea {
    resize:none;
    font-size: 20px;
    color: black;
    font-weight: bold
  }
  #sendBottom{border-top: 2px solid black}
</style>



