<template>
  <div ref="content" id="chatroom" style="height: 480px;overflow-y: scroll;position: fixed;top: 50px;right: 0;left: 0">
<!--    <div v-for="(message,index) in messages">{{index}}&#45;&#45;&#45;&#45;&ndash;&gt;{{message}}</div>-->
    <div v-for="mes in who" :class="getMesseger(mes.userName)">
      <div>
<!--        <img v-if="getAvatar(mes.name)" :src="avarats[sendAva]">-->
<!--        <img v-else :src="avarats[receiveAva]">-->
        <img :src="avarats[parseInt(getObjectValues(mapImg, mes.userName))]" alt="">
      </div>
      <div>{{mes.userName}}</div>
      <div style="word-break:break-all;">
        <div :class="getClasses(mes.userName)"></div>
        <span v-if="mes.msgType==='1'">{{mes.content}}</span>
        <span v-else>{{mes.content}}</span>
<!--        <span v-else><a :href="mes.content" target="_blank" >{{mes.content}}</a></span>-->
      </div>
    </div>
  </div>
</template>

<script>

  import storage from "../../utils/storage";

  export default {
    name: "chatroom",
    props:{
      meses: Array,
    },
    data() {
      return {
        messages: this.$store.state.messages,
        avarats: [
          require('assets/img/login_ava/boy.png'),
          require('assets/img/login_ava/girl.png'),
          require('assets/img/login_ava/geek.png'),
          require('assets/img/login_ava/alien.png'),
          require('assets/img/login_ava/group.png'),
          require('assets/img/login_ava/robot.png')
        ],
        sendAva: '',
        receiveAva: '',
        mapImg: this.$store.state.mapImg,
        url: "http://192.168.123.4:8083/downloadfile/00558af5448347f995e58de8edc546e6.jpg"
      }
    },
    methods: {
      getObjectValues(arr, index) {
      for (var i in arr)
        if(Object.keys(arr[i])[0] === index)
          return arr[i][index]
      },
      getClasses(mes) {
        return mes===this.$store.state.globalUserName ? 'chat-right_triangle' : 'chat-left_triangle'
      },
      getMesseger(mes) {
        return mes===this.$store.state.globalUserName ? 'chat-receiver' : 'chat-sender'
      },
    },
    computed: {
      who() {
        return this.meses
        // let group = []
        // for (let i = 0; i < this.meses.length; i++) {
        //   let sp = this.meses[i]
        //   let obj = {
        //     name: sp.userName,
        //     msgType: sp.msgType,
        //     message: sp.content
        //   };
        //   group.push(obj)
        // }
        // return group
      },
    },
    created() {

    },
    destroyed() {

    },
    mounted() {

    },
    activated() {
      this.sendAva = parseInt(this.$route.query.myAva)
      this.receiveAva = parseInt(this.$route.query.ava)
      this.$nextTick(() => {
        this.$refs.content.scrollTop = this.$refs.content.scrollHeight;
      })
      // let _this = this; //声明一个变量指向Vue实例this，保证作用域一致
      // this.timer = setInterval(() => {
      //   _this.$nextTick(() => {
      //     _this.$refs.content.scrollTop = _this.$refs.content.scrollHeight;
      //   })
      // }, 1000);

    },
    deactivated() {
      // console.log('deactivated')
      // if (this.timer) {
      //   clearInterval(this.timer); // 在Vue实例销毁前，清除我们的定时器
      // }
    },
    beforeRouteLeave (to, from, next) {
      console.log('leave');
      next()
    }
  }
</script>

<style scoped>
  @import "../../assets/css/chatroom.css";
</style>



